.shadow{
  box-shadow:
    0 0 60px 30px #fff,  /* inner white */
    0 0 100px 60px #ff9900, /* middle #ffa31a */
    0 0 200px 100px #ffff00; /* outer #ffff1a */;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #fff;
    position:absolute;
    top:50%;
    left: 50%;
    transform: translate(-50%, -50%);
}