body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
@font-face {
  font-family: "Jersey M54";
  src: local("Russian"),
    url("./fonts/Ambit-Bold.ttf") format("truetype");
  font-weight: normal;
}